import React from "react";

//CSS
import "./produtos.css";

import Carousel from 'react-multi-carousel';
import 'react-multi-carousel/lib/styles.css';

//[Imagem]
//[Marca]
//Bateria [Amperagem]Ah - [Tempo] de garantia
//[PreÃ§o]
//[qtd]x de [preÃ§o/qtd]

const responsive = {
    desktop: {
        breakpoint: {
            max: 3000,
            min: 769
        },
        items: 3,
        partialVisibilityGutter: 40
    },
    tablet: {
        breakpoint: {
            max: 768,
            min: 426
        },
        items: 3
    },
    mobile: {
        breakpoint: {
            max: 425,
            min: 0
        },
        items: 1.75
    }
}

const Item = () => {
    return (
        <article>
            <div>
                <div className="container-img">
                    <img src="https://cdn.awsli.com.br/1000x1000/515/515778/produto/19098387/c6dcf80c3a.jpg" alt=""/>
                </div>
                <div className="dados-produto">
                    <h1 className="texto-bold">Moura</h1>
                    <p className="texto-light">Bateria 40Ah - 9 meses de garantia</p>
                    <p className="texto-bold preco">R$200,00</p>
                </div>
            </div>
        </article>
    )
}

const Produtos = () => {
    return (
        <section id="produtos" className="container-wd">
            <h1 className="titulo texto-preto">Produtos</h1>
            <ul className="filtros">
                <li>
                    <label htmlFor="marca">Marca: </label>
                    <select name="marca">
                        <option value={0}>Selecione</option>
                    </select>
                </li>
                <li>
                    <label htmlFor="tipo">Tipo: </label>
                    <select name="tipo">
                        <option value={0}>Selecione</option>
                    </select>
                </li>
            </ul>
            <Carousel partialVisible focusOnSelect minimumTouchDrag={0} responsive={responsive} draggable={false}>
                <Item/>
                <Item/>
                <Item/>
                <Item/>
            </Carousel>
        </section>
    )
}

export default Produtos;